
.shopping {
  .cartBox {
    display: flex;
    justify-content: center;
    height: 100%;
    overflow-y: scroll;
    .cart {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      margin: 17px 0 0;
      .shop {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 5px;
        .shop-top {
          width: 100%;
          display: flex;
          justify-content: center;
          .store {
            width: 90%;
            display: flex;
            justify-content: space-between;
            margin: 0 0 17px;

            .title {
              font-size: 14px;
              font-weight: normal;
              color: #323332;
              line-height: 14px;
            }
          }
        }
        .shop-center {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          .container {
            width: 90%;
            display: flex;
            margin: 0 0 17px 0;
            padding: 0 0 17px 0;
            border-bottom: 1px solid #f6f7f6;

            .picture {
              width: 0.86rem;
              height: 0.86rem;
              object-fit: cover;
              // min-width: 0.86rem;
            }

            .information {
              width: 100%;
              display: flex;
              flex-direction: column;
              margin: 0 0 0 8px;
              position: relative;

              .title {
                font-size: 16px;
                font-weight: normal;
                color: #323332;
                line-height: 23px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
              }

              .info {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin: 17px 0 0;
                position: absolute;
                bottom: 0;

                .info-left {
                  display: flex;

                  .symbol {
                    font-size: 14px;
                    font-weight: 600;
                    color: #ff2a00;
                    line-height: 22px;
                  }

                  .price {
                    font-size: 18px;
                    font-weight: 600;
                    color: #ff2a00;
                    line-height: 22px;
                  }
                }

                .info-right {
                  display: flex;

                  .inventory {
                    font-size: 12px;
                    font-weight: normal;
                    color: #989998;
                    line-height: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .cartBox::-webkit-scrollbar {
    display: none;
  }
  .contentBox {
    display: flex;
    justify-content: center;

    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      border-radius: 5px;

      .content-top {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-bottom: 1px solid#F6F7F6;

        .spec {
          width: 90%;
          display: flex;
          justify-content: space-between;
          margin: 20px 0 30px;

          .title {
            font-size: 14px;
            font-weight: normal;
            color: #323332;
            line-height: 14px;
          }

          img {
            width: 13px;
            height: 13px;
          }
        }

        .goods {
          width: 90%;
          display: flex;
          margin: 0 0 17px 0;

          .picture {
            width: 0.86rem;
            height: 0.86rem;
            object-fit: cover;
          }

          .information {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin: 0 0 0 8px;
            position: relative;

            .title {
              font-size: 16px;
              font-weight: normal;
              color: #323332;
              line-height: 23px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }

            .info {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin: 17px 0 0;
              position: absolute;
              bottom: 0;

              .info-left {
                display: flex;

                .symbol {
                  font-size: 14px;
                  font-weight: 600;
                  color: #ff2a00;
                  line-height: 22px;
                }

                .price {
                  font-size: 18px;
                  font-weight: 600;
                  color: #ff2a00;
                  line-height: 22px;
                }
              }

              .info-right {
                display: flex;

                .inventory {
                  font-size: 12px;
                  font-weight: normal;
                  color: #989998;
                  line-height: 12px;
                }
              }
            }
          }
        }
      }

      .content-center {
        width: 100%;
        display: flex;
        justify-content: center;
        border-bottom: 1px solid#F6F7F6;
        .spec {
          width: 90%;
          display: flex;
          flex-direction: column;

          .spec-top {
            display: flex;
            flex-direction: column;
            margin: 16px 0;
            .information {
              display: flex;
              flex-direction: column;
              margin: 0 0 5px;
              .information-top {
                display: flex;
                .caption {
                  font-size: 13px;
                  color: #333;
                  font-weight: 600;
                }
              }
              .information-bottom {
                display: flex;
                flex-wrap: wrap;
                margin: 10px 0 0;

                .container {
                  border-radius: 4px;
                  border: 1px solid #eeefee;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  margin: 0 15px 12px 0;
                  padding: 2px 10px;
                  &.active {
                    border-color: #e13734;
                    color: #e13734;
                    background: rgba(225, 55, 52, 0.1);
                  }
                }
              }
            }
          }
          .spec-bottom {
            display: flex;
            margin: 16px 0;
            .message {
              font-size: 13px;
              font-weight: normal;
              color: #666666;
              line-height: 20px;
            }
          }
        }
      }

      .content-bottom {
        width: 100%;
        display: flex;
        justify-content: center;

        .count {
          width: 90%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin: 16px 0 0;

          .title {
            font-size: 13px;
            font-weight: normal;
            color: #666666;
            line-height: 13px;
          }
        }
      }
    }
  }

  .operationBox {
    display: flex;
    justify-content: center;

    .operation {
      width: 100%;
      display: flex;
      justify-content: center;
      position: fixed;
      left: 0;
      bottom: 0;

      .buy {
        width: 3.51rem;
        height: 0.4rem;
        background: linear-gradient(270deg, #ff6f36 0%, #ff0800 100%);
        border-radius: 29px;
        margin: 5px 0;
        font-size: 14px;
        font-weight: normal;
        color: #fefffe;
        line-height: 14px;
      }
    }
  }
}
