
.barrage {
  width: 100%;
  display: flex;
  justify-content: center;
  .comment {
    width: 100%;
    display: flex;
    justify-content: center;
    // background: #f6f6f6;
    .comment-wrap {
      width: 100%;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
      .content {
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 5px 0 0;
        .container {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          .container-top {
            width: 100%;
            display: flex;
            margin: 0 0 10px 0;
            .information {
              display: flex;
              background: rgba(0, 0, 0, 0.3);
              border-radius: 6px;
              padding: 5px;
              word-break: break-all;
              .information-left {
                display: flex;
                .profile {
                  width: 26px;
                  height: 26px;
                  border-radius: 50%;
                  vertical-align: middle;
                }
              }
              .information-right {
                display: flex;
                flex-direction: column;
                margin: 0 0 0 5px;
                .name {
                  font-size: 15px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ffffff;
                  vertical-align: middle;
                }
                img {
                  width: 140px;
                  height: 100px;
                  object-fit: cover;
                  margin: 9px 12px 12px 0;
                  border-radius: 6px;
                }
                .message {
                  font-size: 14px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ffffff;
                  vertical-align: middle;
                }
              }
            }
          }
          .container-center {
            display: flex;
            .gift {
              display: flex;
              // background: rgba(255, 42, 0, 0.3);
              background: rgba(0, 0, 0, 0.3);
              border-radius: 12px;
              padding: 5px 20px;
              .name,
              .message {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                // color: #ff2a00;
                color: #fff;
                line-height: 12px;
              }
              .give {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                // color: #ff2a00;
                color: #fff;
                line-height: 12px;
                margin: 0 5px;
              }
            }
          }
          .container-bottom {
            display: flex;
            flex-direction: column;
            align-items: center;
            .banned,
            .relieve {
              display: flex;
              // background: rgba(255, 42, 0, 0.3);
              background: rgba(0, 0, 0, 0.3);
              border-radius: 12px;
              padding: 5px 20px;
              margin: 0 0 10px 0;
              .name,
              .message {
                font-size: 12px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ff2a00;
                line-height: 12px;
              }
            }
          }
        }
      }
    }
    .comment-wrap::-webkit-scrollbar {
      display: none;
    }
  }
}
