
.live {
  min-height: 100vh;
  background: #f6f6f6;
  .headerBox {
    display: flex;
    justify-content: center;
    .header {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 7px 0 0;
      position: absolute;
      z-index: 10;
      .header-top {
        width: 90%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .stream {
          display: flex;
          background: rgba(0, 0, 0, 0.26);
          border-radius: 41px;

          .stream-status {
            display: flex;
            width: 0.6rem;
            height: 0.26rem;
            display: flex;
            justify-content: center;
            align-items: center;
            background: rgba(0, 0, 0, 0.26);
            border-radius: 0 41px 41px 0;
            img {
              width: 10px;
              height: 10px;
            }

            .message {
              font-size: 11px;
              zoom: 0.9;
              font-weight: normal;
              color: #fff;
              margin: 0 0 0 2px;
              line-height: 16px;
            }
          }

          .stream-theme {
            width: 1.88rem;
            display: flex;
            align-items: center;

            .title {
              font-size: 14px;
              font-weight: normal;
              color: #fff;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              line-height: 16px;
              margin: 0 13px 0 7px;
            }
          }
        }
        .rank {
          width: 0.6rem;
          height: 0.26rem;
          display: flex;
          background: rgba(0, 0, 0, 0.26);
          border-radius: 41px;
          align-items: center;
          img {
            width: 14px;
            height: 14px;
            margin: 0 1px 0 5px;
          }
          .message {
            font-size: 11px;
            zoom: 0.9;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #ffffff;
          }
        }
      }
      .header-center {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin: 8px 0 0;
        .introduce-bottom {
          width: 1.7rem;
          height: 0.24rem;
          border-radius: 118px;
          display: flex;
          align-items: center;
          background: rgba(0, 0, 0, 0.26);
          margin: 0 0 0 18px;

          .introduce-bottom-left {
            display: flex;

            .brief_normal {
              display: flex;
              .message {
                font-size: 11px;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 0 3px 0 8px;
                zoom: 0.9;
              }
            }
            .brief_active {
              display: flex;
              .message {
                font-size: 11px;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 0 3px 0 8px;
                zoom: 0.9;
              }
            }
          }

          .introduce-bottom-right {
            display: flex;
            margin: 0 0 0 15px;

            .browse {
              display: flex;
              img {
                width: 12px;
                height: 12px;
              }

              .number {
                font-size: 11px;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 1px 0 0 3px;
                zoom: 0.9;
              }
            }

            .like {
              display: flex;
              margin: 0 0 0 5px;
              img {
                width: 12px;
                height: 12px;
              }

              .number {
                font-size: 11px;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 1px 0 0 3px;
                zoom: 0.9;
              }
            }
          }
        }

        .photo {
          display: flex;
          align-items: center;
          width: 0.92rem;
          height: 0.24rem;
          background: rgba(0, 0, 0, 0.26);
          border-radius: 41px 0px 0px 41px;
          .text {
            width: 12px;
            height: 12px;
            margin: 0 0 0 9px;
          }
          .message {
            font-size: 11px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #ffffff;
            zoom: 0.9;
            margin: 0 5px 0 1px;
          }
          img {
            width: 6px;
            height: 11px;
          }
        }
      }

      .header-bottom {
        width: 90%;
        display: flex;
        justify-content: flex-end;
        margin: 8px 0 0;
      }
    }
  }
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .content-top {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .movie {
          width: 100%;
          height: 2.23rem;
          img {
            width: 100%;
            height: 100vh;
            object-fit: cover;
          }
        }

        .information {
          width: 95%;
          display: flex;
          flex-direction: column;
          position: absolute;
          .container {
            width: 1.95rem;
            display: flex;
            height: 44px;
            background: rgba(255, 42, 0, 0.34);
            border-radius: 230px;
            .profile {
              width: 36px;
              height: 36px;
              margin: 4px 0 4px 5px;
              border-radius: 50%;
            }
            .info {
              display: flex;
              flex-direction: column;
              .name {
                width: 0.9rem;
                font-size: 15px;
                font-family: PingFang SC-粗体, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 8px 0 4px 4px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
              .message {
                font-size: 12px;
                font-family: PingFang SC-常规, PingFang SC;
                font-weight: normal;
                color: #ffffff;
                line-height: 13px;
                margin: 0 0 0 5px;
              }
            }
            .gift {
              display: flex;
              align-items: flex-end;
              img {
                width: 68px;
                height: 68px;
              }
            }
          }
        }
      }
      .content-center {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        // background: #fff;
        .introduce {
          width: 92%;
          display: flex;
          flex-direction: column;
          align-items: center;
          margin: 5px 0;
          visibility: hidden;
          .introduce-top {
            width: 100%;
            display: flex;
            .stream {
              display: flex;
              .stream-status {
                display: flex;
                width: 0.58rem;
                height: 0.26rem;
                display: flex;
                align-items: center;
                border-radius: 41px;
                background: #f4f4f4;
                img {
                  width: 10px;
                  height: 10px;
                  margin: 0 3px 0 6px;
                }
                .message {
                  font-size: 0.786em;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  zoom: 0.9;
                }
              }
              .stream-theme {
                width: 2.9rem;
                display: flex;
                align-items: center;
                margin: 0 0 0 5px;
                .title {
                  font-size: 16px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #323332;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  line-height: 16px;
                }
              }
            }
          }
          .introduce-bottom {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 6px 0 0;
            .introduce-bottom-left {
              display: flex;
              .brief_normal {
                display: flex;
                border-bottom: 2px solid #999999;
                padding: 0 0 5px 0;
                .message {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #999999;
                  line-height: 13px;
                }
              }
              .brief_active {
                display: flex;
                border-bottom: 2px solid #ff2a00;
                padding: 0 0 5px 0;
                .message {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 13px;
                }
              }
              img {
                width: 6px;
                height: 11px;
                margin: 2px 0 0 7px;
              }
            }
            .introduce-bottom-right {
              display: flex;
              .browse {
                display: flex;
                img {
                  width: 12px;
                  height: 12px;
                }
                .number {
                  font-size: 13px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #999999;
                  line-height: 13px;
                  margin: 0 0 0 4px;
                }
              }
            }
          }
        }

        .describe {
          width: 100%;
          display: flex;
          justify-content: center;
          margin: 10px 0 13px;
          overflow-y: scroll;
          -webkit-overflow-scrolling: touch;
          background: #f6f6f6;
          z-index: 10;
          .message {
            font-size: 15px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #323332;
            line-height: 25px;
          }
        }
        .describe::-webkit-scrollbar {
          display: none;
        }
        .review {
          width: 100%;
          display: flex;
          position: absolute;
          .start {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
          }
          .lucky_bag {
            width: 0.58rem;
            height: 0.63rem;
            position: absolute;
            right: 10px;
            bottom: 90px;
            background: url("@/assets/lucky_bag.png") no-repeat center;
            background-size: 100% 100%;
            display: flex;
            justify-content: center;
            z-index: 10;
            .time {
              position: absolute;
              bottom: 3px;
              font-size: 11px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ffffff;
              line-height: 14px;
              letter-spacing: 2px;
              zoom: 0.9;
            }
          }
          .lucky_turnplate {
            position: absolute;
            right: 10px;
            bottom: 10px;
            z-index: 10;
            img {
              width: 58px;
              height: 63px;
            }
          }

          .goodsBox {
            width: 100%;
            display: flex;
            justify-content: center;
            position: absolute;
            bottom: 0;

            .goods {
              width: 95%;
              display: flex;
              background: #fefffe;
              border-radius: 8px;

              .picture {
                width: 0.86rem;
                height: 0.86rem;
                object-fit: cover;
                margin: 8px;
              }

              .goods_info {
                display: flex;
                flex-direction: column;
                margin: 8px 10px 8px 0;

                .title {
                  font-size: 16px;
                  font-weight: normal;
                  color: #323332;
                  line-height: 23px;
                  display: -webkit-box;
                  -webkit-box-orient: vertical;
                  -webkit-line-clamp: 2;
                  overflow: hidden;
                }

                .info {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  margin: 17px 0 0;

                  .info-left {
                    display: flex;
                    align-items: center;
                    .symbol {
                      font-size: 14px;
                      font-weight: 600;
                      color: #ff2a00;
                      line-height: 22px;
                    }

                    .price {
                      font-size: 18px;
                      font-weight: 600;
                      color: #ff2a00;
                      line-height: 22px;
                    }

                    .sell {
                      font-size: 12px;
                      font-weight: normal;
                      color: #999999;
                      line-height: 12px;
                      margin: 0 0 0 7px;
                    }
                  }

                  .info-right {
                    display: flex;

                    .buy {
                      width: 0.61rem;
                      height: 0.21rem;
                      background: linear-gradient(270deg, #fe0d38 0%, #fc811f 100%);
                      border-radius: 20px;
                      font-size: 13px;
                      font-weight: normal;
                      color: #fefffe;
                      line-height: 13px;
                    }
                  }
                }
              }
            }
          }
        }
      }
      .content-bottom {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        position: fixed;
        bottom: 0;
        z-index: 10;
        .send {
          width: 95%;
          min-height: 0.5rem;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .send-left {
            width: 2.59rem;
            height: 0.4rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .shopping {
              display: flex;
              img {
                width: 26px;
                height: 26px;
              }
            }
            .input_box {
              width: 2.59rem;
              height: 0.4rem;
              display: flex;
              justify-content: space-between;
              align-items: center;
              background: rgba(0, 0, 0, 0.3);
              border-radius: 170px;
              .text {
                width: 80%;
                height: 0.3rem;
                margin: 0 0 0 14px;
                display: inline-block;
                border: none;
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                line-height: 14px;
                background: none;
                color: #fff;
              }
              .text::-webkit-input-placeholder {
                color: #ccc;
              }
              .smile {
                width: 18px;
                height: 18px;
                margin: 0 14px 0 0;
              }
            }
          }
          .send-center {
            display: flex;
            align-items: center;
            .add {
              width: 0.8rem;
              height: 0.36rem;
              background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
              border-radius: 34px;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ffffff;
              line-height: 14px;
            }
          }
          .send-right {
            display: flex;
            align-items: center;
            .gift {
              display: flex;
              margin: 0 23px 0 0;
              img {
                width: 26px;
                height: 26px;
              }
            }
            .more {
              display: flex;
              margin: 0 10px 0 0;
              img {
                width: 26px;
                height: 26px;
              }
            }
          }
        }
        .expression {
          overflow-y: scroll;
          -webkit-overflow-scrolling: touch;
          ::v-deep .emoji-mart {
            width: 100% !important;
            height: 160px;
            border: none;
            background: #eee;
            .emoji-mart-category-label {
              display: none;
            }
          }
        }
      }
      @supports (bottom: constant(safe-area-inset-bottom)) or
        (bottom: env(safe-area-inset-bottom)) {
        .content-bottom {
          padding-bottom: constant(safe-area-inset-bottom);
          padding-bottom: env(safe-area-inset-bottom);
        }
      }
      .restBox {
        display: flex;
        justify-content: center;
        .rest {
          width: 100%;
          display: flex;
          flex-direction: column;
          .rest-top {
            display: flex;
            justify-content: space-between;
            background: #fff;
            .account {
              display: flex;
              margin: 13px 0 13px 20px;
              .title {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #333333;
                line-height: 21px;
              }
              .number {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #ffb300;
                line-height: 21px;
              }
            }
            .close {
              width: 28px;
              height: 21px;
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #ff2a00;
              line-height: 21px;
              margin: 13px 20px 13px 0;
            }
          }
          .rest-center {
            display: flex;
            flex-direction: column;
            background: #f6f6f6;
            padding: 0 0 28px;
            .chat {
              display: flex;
              flex-direction: column;
              margin: 23px 0 0 20px;
              .chat-top {
                display: flex;
                .title {
                  font-size: 14px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                }
              }
              .chat-bottom {
                display: flex;
                position: relative;
                img {
                  width: 50px;
                  height: 50px;
                  margin: 18px 0 7px;
                }
                .message {
                  font-size: 12px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #333333;
                  line-height: 12px;
                  margin: 0 0 0 6px;
                  position: absolute;
                  left: 0;
                  bottom: 0;
                }
              }
            }
            .streaming {
              display: flex;
              flex-direction: column;
              margin: 22px 0 0 20px;
              .streaming-top {
                display: flex;
                .title {
                  font-size: 14px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                }
              }
              .streaming-bottom {
                display: flex;
                .gold_convert,
                .winning_record,
                .give_record,
                .order_record,
                .share {
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  margin: 0 22px 0 0;
                  img {
                    width: 50px;
                    height: 50px;
                    margin: 20px 0 5px;
                  }
                  .message {
                    font-size: 12px;
                    font-family: PingFang SC-中等, PingFang SC;
                    font-weight: normal;
                    color: #333333;
                    line-height: 12px;
                  }
                }
              }
            }

            .manage {
              display: flex;
              flex-direction: column;
              margin: 22px 0 0 20px;
              .manage-top {
                display: flex;
                .title {
                  font-size: 14px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #ff2a00;
                  line-height: 10px;
                }
              }
              .manage-bottom {
                display: flex;
                .lucky_bag,
                .turnplate,
                .manager,
                .ban {
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  margin: 0 22px 0 0;
                  img {
                    width: 50px;
                    height: 50px;
                    margin: 20px 0 5px;
                  }
                  .message {
                    font-size: 12px;
                    font-family: PingFang SC-中等, PingFang SC;
                    font-weight: normal;
                    color: #333333;
                    line-height: 12px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.lucky_bag_dialog {
  display: flex;
  .information {
    display: flex;
    flex-direction: column;
    align-items: center;
    .information-top {
      display: flex;
      margin: 43px 0 0;
      .title {
        font-size: 20px;
        font-family: PingFang SC-粗体, PingFang SC;
        font-weight: normal;
        color: #ff2a00;
        line-height: 20px;
      }
    }
    .information-center {
      display: flex;
      margin: 30px 0;
      .message {
        font-size: 16px;
        font-family: PingFang SC-中等, PingFang SC;
        font-weight: normal;
        color: #333333;
        line-height: 18px;
      }
    }
    .information-bottom {
      display: flex;
      margin: 0 0 28px;
      .apply {
        width: 1.62rem;
        height: 0.48rem;
        background: linear-gradient(180deg, #ff5736 0%, #ff2a00 100%);
        border-radius: 24px;
        font-size: 16px;
        font-family: PingFang SC-中等, PingFang SC;
        font-weight: normal;
        color: #ffffff;
        line-height: 16px;
      }
    }
  }
}
.remind_dialog {
  display: flex;
  .remind {
    display: flex;
    flex-direction: column;
    align-items: center;
    .title {
      font-size: 20px;
      font-family: PingFang SC-粗体, PingFang SC;
      font-weight: normal;
      color: #ff2a00;
      line-height: 20px;
      margin: 43px 0 0;
    }
    .message {
      font-size: 16px;
      font-family: PingFang SC-中等, PingFang SC;
      font-weight: normal;
      color: #333333;
      line-height: 18px;
      margin: 30px 0;
    }
    .confirm {
      width: 1.62rem;
      height: 0.48rem;
      background: linear-gradient(180deg, #ff5736 0%, #ff2a00 100%);
      border-radius: 24px;
      font-size: 16px;
      font-family: PingFang SC-中等, PingFang SC;
      font-weight: normal;
      color: #ffffff;
      line-height: 16px;
      margin: 0 0 44px;
    }
  }
}

.warning_dialog {
  display: flex;
  .warning {
    display: flex;
    flex-direction: column;
    align-items: center;
    .title {
      font-size: 20px;
      font-family: PingFang SC-粗体, PingFang SC;
      font-weight: normal;
      color: #ff2a00;
      line-height: 20px;
      margin: 43px 0 0;
    }
    .message {
      font-size: 16px;
      font-family: PingFang SC-中等, PingFang SC;
      font-weight: normal;
      color: #333333;
      line-height: 24px;
      margin: 30px;
    }
    .confirm {
      width: 1.62rem;
      height: 0.48rem;
      background: linear-gradient(180deg, #ff5736 0%, #ff2a00 100%);
      border-radius: 24px;
      font-size: 16px;
      font-family: PingFang SC-中等, PingFang SC;
      font-weight: normal;
      color: #ffffff;
      line-height: 16px;
      margin: 0 0 28px;
    }
  }
}
