
.liveLike {
  position: absolute;
  right: 0;
  bottom: 10px;
  .live_like {
    width: 0.4rem;
    height: 0.4rem;
    background: url("https://res1.qdszgh.cn/%E9%B8%BF%E9%9B%81%E8%9E%8D%E7%9B%B4%E6%92%AD/heart.png")
      no-repeat center;
    background-size: 100% 100%;
    position: absolute;
    right: 20px;
    bottom: 0;
    .count {
      display: inline-block;
      font-size: 13px;
      color: #fff;
      background: linear-gradient(90deg, #fa4454, #eb1d5a);
      position: absolute;
      padding: 0 4px;
      border-radius: 15px;
      left: 50%;
      /* 定位父级的50% */
      transform: translateX(-50%);
      /*自己的50% */
    }
  }
}
